import React, { useState } from 'react';

import Typography from '@mui/material/Typography';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Drawer from '@mui/material/Drawer';
import MenuIcon from '@mui/icons-material/Menu';
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';

import CalendarIcon from '@mui/icons-material/CalendarMonth';
import EuroIcon from '@mui/icons-material/Euro';
import HelpIcon from '@mui/icons-material/ContactSupport';

import { useNavigate } from "react-router-dom";

const Header = (): JSX.Element => {
    const [open, setOpen] = useState(false);
    const toggleDrawer = (newOpen: boolean) => () => {
        setOpen(newOpen);
    };
    const navigate = useNavigate();
    return (
        <>
            <AppBar position="static">
                <Toolbar>
                    <IconButton
                        size="large"
                        edge="start"
                        color="inherit"
                        aria-label="menu"
                        sx={{ mr: 2 }}
                        onClick={toggleDrawer(true)}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" color="inherit" component="div">
                        Лунный календарь
                    </Typography>
                </Toolbar>
            </AppBar>
            <Drawer open={open} onClose={toggleDrawer(false)}>
                <Box sx={{ width: 250 }} role="presentation" onClick={toggleDrawer(false)}>
                    <List>
                        <ListItem key="Календарь" disablePadding>
                            <ListItemButton onClick={() => navigate("/")}>
                                <ListItemIcon>
                                    <CalendarIcon />
                                </ListItemIcon>
                                <ListItemText primary="Календарь" />
                            </ListItemButton>
                        </ListItem>
                        <ListItem key="Подписка" disablePadding>
                            <ListItemButton onClick={() => navigate("/subscribe")}>
                                <ListItemIcon>
                                    <EuroIcon />
                                </ListItemIcon>
                                <ListItemText primary="Подписка" />
                            </ListItemButton>
                        </ListItem>
                        <ListItem key="О программе" disablePadding>
                            <ListItemButton onClick={() => navigate("/about")}>
                                <ListItemIcon>
                                    <HelpIcon />
                                </ListItemIcon>
                                <ListItemText primary="О программе" />
                            </ListItemButton>
                        </ListItem>
                    </List>
                </Box>
            </Drawer>
        </>
    );
};

export default Header;