import React, {useState} from 'react';
import ReactGA from 'react-ga4';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

const InstallCard = (): JSX.Element => {
    const [visible, setVisible] = useState(false);
    const [installPrompt, setInstallPrompt] = useState<any>(null);
    window.addEventListener("beforeinstallprompt", (event) => {
        event.preventDefault();
        setInstallPrompt(event);
        setVisible(true);
    });
    const install = async () => {
        if (!installPrompt) {
            return;
        }
        const result = await installPrompt.prompt();
        console.log('install finished as', result)
        ReactGA.event({
            category: 'User Engagement',
            action: 'install_' + (result ? 'ok' : 'fail'),
            label: 'Installation ' + (result ? 'ok' : 'fail'),
        });
        setVisible(false);
        setInstallPrompt(null);
    };
    const close = () => {
        ReactGA.event({
            category: 'User Engagement',
            action: 'close_install_popup',
            label: 'Installation popup closed',
        });
        setVisible(false);
        setInstallPrompt(null);
    };

    if (!visible) {
        return <></>
    }
    return (
        <Box sx={{position: "absolute", bottom: 10, left: 10, maxWidth: '50%', minWidth: '25%'}}>
            <Card variant="outlined">
                <CardContent>
                    <Typography variant='h6'>
                        Удобнее на мобильном телефоне
                    </Typography>
                    <Typography variant='body2'>
                        Установите Лунный календарь к себе на телефон для более удобного доступа!
                    </Typography>
                </CardContent>
                <CardActions>
                    <Button size="small" onClick={install}>Установить</Button>
                    <Button size="small" onClick={close}>Не сейчас</Button>
                </CardActions>
            </Card>
        </Box>
    );
};

export default InstallCard;